@import './../../styles/color.scss';
@import './../../styles/responsive.scss';
@import './../../styles/fonts.scss';

.facilityContainer {
  display: flex;
  margin-top: 15px;
}
.circleDiv {
  margin-right: 6px;
  margin-bottom: 3px;
  padding: 5px;
  border-radius: 20px;
  border: 1px solid rgb(108, 191, 95);
  width: 30px;
  height: 30px;
  img {
    width: 18px;
    height: 18px;
  }
}
.moneyDark {
  color: $color-text-dark-grey;
}
.f14 {
  font-size: 14px;
  margin-top: 15px;
  color: #444444;
  font-weight: 600;
}
.f14grey {
  color: $color-text-grey2;
  font-size: 14px;
  margin-bottom: 12px;
}
.moneyLight {
  color: $color-text-grey3;
}
.suggestMenu {
}

.br {
  border-bottom: 0.5px solid rgb(226, 232, 240);
  width: 65%;
  margin: 14px 0px;
}
.sep {
  margin: 0px 15px;
}
.tagContainer {
  .tag {
    background-color: $color-bg-green;
    margin: 0px 8px 0px 16px;
    border-radius: 2px;
    padding: 0 7px;
    font-size: 12px;
    font-weight: 600;
    color: white;
  }
  .tag2 {
    background-color: #999999;
    margin: 0px 8px 0px 16px;
    border-radius: 2px;
    padding: 0 7px;
    font-size: 12px;
    font-weight: 600;
    color: white;
  }
}

.rightBox {
  width: 100%;
  //   display: flex;
  flex-direction: column;
  @include tablet {
    display: none;
  }
  @include desktop {
    display: flex;
  }
  .merchantContainer {
    background-color: white;
    display: flex;
    margin: 5px;
    // border: 1px solid black;
    padding: 5px;
    .imgContainer {
      overflow: hidden;
      width: 250px;
      height: 234px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 2px;
      .merchantImg {
        @media (min-width: 1024px) {
          min-width: 250px; /*width of parent container*/
          max-width: 380px;
          min-height: 350px; /*height of parent container*/
          max-height: 380px;
          object-fit: cover;
          position: relative;
          border-radius: 2px;
        }
      }
    }
    .merchantDetail {
      //   border: 1px solid green;
      height: 234px;
      width: calc(100% - 234px);
      padding: 14px;
      .cardHeader {
        display: flex;
        .cardName {
          color: black;
          font-size: 20px;
          font-weight: 600;
        }
      }
      .cardSubHeader {
        display: flex;
        color: $color-text-grey2;
        flex-wrap: wrap;
      }
    }
  }
}
